import { NavLink } from "react-router-dom";
//
import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import Typography from '@mui/material/Typography';
import CardHeader from '@mui/material/CardHeader';
import CardContent from '@mui/material/CardContent';
import CardActionArea from '@mui/material/CardActionArea';
import LinearProgress from '@mui/material/LinearProgress';
import Avatar from '@mui/material/Avatar';
import { blue } from '@mui/material/colors';
//
import progressService from '../../../API/progressService';
//
const LinearProgressWithLabel = ({ value, color }) => {
    const labelText = `${Math.round(value)}%`
    return (
        <Box sx={{ display: 'flex', alignItems: 'center' }}>
            <Box sx={{ width: '100%', mr: 1 }}>
                <LinearProgress variant="determinate" value={value} color={color} />
            </Box>
            <Box sx={{ minWidth: 35 }}>
                <Typography variant="body2" color="primary">
                    {labelText}
                </Typography>
            </Box>
        </Box>
    );
}
//
const getProgressColor = (progress, errors) => {
    if (progress === 0 && errors === 0)
        return "primary"
    return (errors > 0) ? "error" : "success";
}
//
const CategoryCard = ({ category }) => {
    const path = `training/:${category.key}`
    const { progress, errors } = progressService.getProgress(category.key)
    const stats = (errors > 0) ?
        `Totall words: ${category.count}, Errors: ${errors}` :
        `Totall words: ${category.count}`
    const logo = category.key.substring(0, 2).toUpperCase()
    const progressColor = getProgressColor(progress, errors);
    return (
        <Card>
            <CardActionArea component={NavLink} to={path}>
                <CardHeader sx={{ pb: 0 }}
                    avatar={<Avatar sx={{ bgcolor: blue[200] }}>{logo}</Avatar>}
                    title={category.value}
                    subheader={stats} />
                <CardContent>
                    <LinearProgressWithLabel value={progress} color={progressColor} />
                </CardContent>
            </CardActionArea>
        </Card>
    )
}

export default CategoryCard